<template>
    <b-tooltip :multilined="true" :label="tooltip" type="is-dark" position="is-top">
        <div class="slot picker">
            <img
                :data="value"
                draggable="true"
                class="slotContent"
                v-if="!isEmpty"
                :src="'data:image/png;base64,' + value.sprite"
            />
        </div>
    </b-tooltip>
</template>

<script>
export default {
    props: {
        value: {
            type: Object,
            default: () => {
                return { sprite: null };
            }
        }
    },
    computed: {
        isEmpty() {
            return typeof this.value.sprite !== "string";
        },
        tooltip() {
            if (!this.value.name) {
                return "";
            }

            return `${this.value.name}:
            ${this.value.description}`;
        }
    },
    methods: {}
};
</script>