export default "function OnPlayerSpawned( player_entity ) \r\n    local init_check_flag = \"starting_wand_$RANDOM_init_done\"\r\n\tif GameHasFlagRun( init_check_flag ) then\r\n\t\treturn\r\n\tend\r\n    GameAddFlagRun( init_check_flag )\r\n    \r\n    local x,y = EntityGetTransform(player_entity)\r\n    local wand_entity = EntityLoad( \"mods/$MOD_FOLDER/files/wand.xml\", x, y )\r\nend";