<template>
    <div class="box">
        <div class="wand-preset" v-for="(val, index) in wandPresets" :key="val.stats.name">
            <span class="label">{{val.stats.name}}</span>
            <WandSlot :value="val.stats.sprite" />
            <span class="label">Always Cast</span>
            <div class="is-inlined" v-for="spell in val.alwaysCast" :key="genKey(spell)">
                <SpellSlot :value="spell" />
            </div>
            <span class="label">Spells</span>
            <div class="is-inlined" v-for="spell in val.deck" :key="genKey(spell)">
                <SpellSlot :value="spell" />
            </div>
            <div class="column">
                <div class="is-centered buttons">
                    <b-button
                        @click="download(val)"
                        type="is-primary"
                        size="is-small"
                        icon-left="download"
                    >Download</b-button>
                    <b-button
                        @click="copyToast"
                        v-clipboard="getWandCode(val)"
                        type="is-primary"
                        size="is-small"
                        icon-left="content-copy"
                    >Code</b-button>
                    <b-button
                        @click="copyToast"
                        v-clipboard="getWandLink(val)"
                        type="is-primary"
                        size="is-small"
                        icon-left="content-copy"
                    >Link</b-button>
                    <b-button
                        @click="deleteWand(index)"
                        type="is-primary"
                        size="is-small"
                        icon-left="trash-can-outline"
                    >Delete</b-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import spellData from "../assets/spellData.json";
import wandsData from "../assets/wandsData.json";
import SpellSlot from "./SpellSlot.vue";
import WandSlot from "./WandSlot.vue";
export default {
    components: {
        SpellSlot,
        WandSlot
    },
    data() {
        return {
            spells: spellData,
            wands: wandsData,
            wandPresets: {}
        };
    },
    beforeMount() {
        let presets = localStorage.getItem("wands");
        if (presets) {
            this.wandPresets = JSON.parse(presets);
        }
    },
    methods: {
        genKey(spell) {
            return `${spell.id}_${Math.random() * 10000}`;
        },
        getWandCode(wand) {
            let tiny = [
                [
                    wand.stats.sprite.id,
                    wand.stats.speed,
                    Number(wand.stats.shuffle),
                    wand.stats.spellsCast,
                    wand.stats.cast,
                    wand.stats.recharge,
                    wand.stats.capacity,
                    wand.stats.spread,
                    wand.stats.mana,
                    wand.stats.mana_recharge
                ]
            ];
            let alwaysCast = wand.alwaysCast.map(this.simplifySpells);
            let deck = wand.deck.map(this.simplifySpells);
            tiny.push(alwaysCast);
            tiny.push(deck);
            let buff = Buffer.from(JSON.stringify(tiny))
            return buff.toString("base64");
        },
        simplifySpells(arr) {
            return arr.i || -1;
        },
        getWandLink(wand) {
            /*
            let data = {
                stats: Object.assign({}, wand.stats),
                alwaysCast: wand.alwaysCast.map(this.simplifySpells),
                deck: wand.deck.map(this.simplifySpells)
            };
            data.stats.sprite = data.stats.sprite.id;
            data = Buffer.from(JSON.stringify(data));
            */
            return `http://${
                window.location.host
            }/noita-loadouts/#/wand-builder?data=${this.getWandCode(wand)}`;
        },
        deleteWand(index) {
            this.wandPresets.splice(index, 1);
            localStorage.setItem("wands", JSON.stringify(this.wandPresets));
        },
        copyToast() {
            this.$buefy.toast.open({ type: "is-success", message: "Copied to clipboard." });
        },
        download(wand) { 
            this.$modgenerator.BuildWandMod(wand)
        }
    }
};
</script>