import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Presets from '../views/Presets.vue'
Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/presets',
    name: 'Presets',
    component: Presets
  },
  {
    path: '/wand-builder',
    name: 'Wand Builder',
    component: function () {
      return import(/* webpackChunkName: "wandbuilder" */ '../views/WandBuilder.vue')
    }
  },
  {
    path: '/loadout',
    name: 'Loadout Builder',
    component: function () {
      return import(/* webpackChunkName: "wandbuilder" */ '../views/LoadoutBuilder.vue')
    }
  }
]

const router = new VueRouter({
  routes
})

export default router
