
import MOD_XML from '../assets/files/mod.xml'
import WAND_SPRITE_XML from '../assets/files/wand_sprite.xml'
import WAND_XML from '../assets/files/wand.xml'

import WAND_SETUP_LUA from '../assets/files/wand_setup.lua'
import INIT_SPAWN_WAND from '../assets/files/init_wand.lua'

import JSZip from 'jszip'
import { saveAs } from 'file-saver'
const ModGenerator = {
    install(Vue) {
        Vue.prototype.$modgenerator = ModGenerator
    },

    parseSpells(v) {
        return v.id ? `"${v.id}"` : 0
    },

    GetWandTable(wand) {
        return `
        local wand = {
            name = "${wand.stats.name}",
            sprite = "${wand.stats.sprite.name}",
            deck_capacity = ${wand.stats.capacity},
            actions_per_round = ${wand.stats.spellsCast},
            reload_time = ${wand.stats.recharge * 60},
            shuffle_deck_when_empty = ${Number(wand.stats.shuffle)},
            fire_rate_wait = ${wand.stats.cast * 60},
            spread_degrees = ${wand.stats.spread},
            speed_multiplier = ${wand.stats.speed ? wand.stats.speed : 1},
            mana_charge_speed = ${wand.stats.mana_recharge},
            mana_max = ${wand.stats.mana},
            always_cast = {${wand.alwaysCast.map(this.parseSpells).toString()}},
            deck = {${wand.deck.map(this.parseSpells).toString()}}
        };
        
        wand_info = wands[${wand.stats.sprite.id + 1}]
        `
    },

    Sanitize(str) {
        return str.replace(/"|'/g, "").replace(/ /g, "-")
    },

    BuildWandMod(wand) {
        let modName = "starting-wand-" + this.Sanitize(wand.stats.name)
        let modFolder = "starting-wand-" + this.Sanitize(wand.stats.name)

        let luaWand = this.GetWandTable(wand)
        
        let modFiles = {
            "mod.xml": MOD_XML.replace("$MOD_NAME", modName),
            "init.lua": INIT_SPAWN_WAND.replace("$MOD_FOLDER", modFolder)
                .replace("$RANDOM", Math.floor(Math.random() * 90000) + 1000),

            "files": {
                "wand_sprite.xml": WAND_SPRITE_XML.replace("$WAND_SPRITE", wand.stats.sprite.name),
                "wand.xml": WAND_XML.replace("$MOD_WAND", this.Sanitize(wand.stats.name))
                    .replace(/\$MOD_FOLDER/gm, modFolder),
                "wand_setup.lua": WAND_SETUP_LUA.replace("$WAND_TABLE", luaWand)
            }
        }

        this.GenerateZip(modName, modFiles)
    },

    GenerateZip(name, files) {
        let zip = new JSZip()
        let rootFolder = zip.folder(name)

        for (const fileName in files) {
            if (typeof files[fileName] == "object") {
                let folder = rootFolder.folder(fileName)

                for (const subFile in files[fileName]) {
                    folder.file(subFile, files[fileName][subFile])
                }
            }
            else {
                rootFolder.file(fileName, files[fileName])
            }
        }

        zip.generateAsync({ type: "blob" }).then((content) => {
            saveAs(content, name + ".zip")
        })
    }

}

export default ModGenerator