<template>
    <div class="slot">
        <img
            :data="value"
            draggable="true"
            class="wandContent"
            v-if="!isEmpty"
            :src="'data:image/png;base64,' + value.sprite"
        />
    </div>
</template>

<script>
export default {
    props: {
        value: {
            type: Object,
            default: () => {
                return { sprite: null };
            }
        }
    },
    computed: {
        isEmpty() {
            return typeof this.value.sprite !== "string";
        }
    },
    methods: {}
};
</script>